import BaseEngine from './baseEngine';

const DEFAULTS = {
    debug: false,
    width: 480,
    height: 480,
    pixelRatio: window.devicePixelRatio,
    clickToggleDebug: true,
    pixelate: 1
};

class SketchEngine extends BaseEngine {
  constructor(canvasEl, opts = {}) {
    super(DEFAULTS, opts);

    this.canvasEl = canvasEl;
    this.canvasCtx = this.canvasEl.getContext('2d');
    window.ctx = this.canvasCtx;

    this.updateDimensions(this.options.width, this.options.height, this.options.pixelRatio, this.options.pixelate);
  }

  get wrapper() {
    return this.canvasEl;
  }

  update(dt) {
    this._renderer({
      debug: this.options.debug,
      frame: this.frame,
      deltaTime: dt,
      width: this.options.width,
      height: this.options.height
    });

    this.frame++;
  }
}

export default SketchEngine;
